<template>
  <div>
    <audio
      :src="currentItem.file"
      @loadedmetadata = 'loadedmetadata'
      @timeupdate = 'timeupdate'
      ref="audio"
    ></audio>
  </div>
</template>
<script>
import {mapGetters, mapState, mapMutations} from 'vuex'
export default {
  computed: {
    audio () {
      return this.$refs.audio
    },
    ...mapState('player', ['paused', 'volume', 'currentTime', 'changeTime', 'duration']),
    ...mapGetters('list', ['currentItem'])
  },
  methods: {
    ...mapMutations('player', ['GET_DURATION', 'GET_CURRENTTIME']),
    loadedmetadata () {
      this.GET_DURATION(this.audio.duration)
    },
    timeupdate () {
      this.GET_CURRENTTIME(this.audio.currentTime)
    }
  },
  mounted () {
    this.audio.oncanplay = () => {
      if (!this.paused) {
        this.audio.play()
      }
    }
  },
  watch: {
    changeTime (val, oldVal) {
      this.audio.currentTime = val
    },
    paused (val, oldVal) {
      if (val) {
        this.audio.pause()
      } else {
        this.audio.play()
      }
    }
  }
}
</script>



