import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/components/HomePage/HomePage'
import Main from '@/components/Main'
import Mmain from '@/components/MusicPlayer/M_main'
import MPlayer from '@/components/MusicPlayer/MPlayer/MPlayer'
import Header from '@/components/MusicPlayer/Header/Header'
import Player from '@/components/MusicPlayer/player/player'

Vue.use(Router)

// mode: 'history',
const routes = [
  {
    path: '/',
    name: 'Main',
    components: {
      default: Main
      // header: Header
    },
    children: [
      {
        path: '/',
        name: 'HomePage',
        component: HomePage

      }
    ]
  },

  {
    path: '/musicPlayer',
    // name: 'M_main',
    components: {
      default: Mmain,
      Header: Header,
      MPlayer: MPlayer
    },
    children: [
      {
        path: '',
        name: 'Player',
        component: Player
      }
    ]

  }

]

export default new Router({
  routes
})



