import {formatTime} from './../../utils/formatTime'

export default {
  leftTime (state) {
    return formatTime(state.duration - state.currentTime)
  },
  durationTime (state) {
    return formatTime(state.duration)
  },
  currentPercentAbsoulte (state) {
    return parseInt(state.currentTime / state.duration * 100)
  }
}



