<template>
  <div class = "player_wrap">
    <div class="container">
      <div class="cow">
        <div class="player_main">
          <div class="player_main_header_wrap">
            <div class="player_main_header_main">
              <div class="header_img" :class="paused ? 'pause' : 'play'">
                <img :src="currentItem.cover" alt="">
              </div>
              <div class="header_title_wrap">
                <div class="header_title">
                  <p class="Stitle">
                    {{currentItem.title}}
                  </p>
                  <hr>
                  <p class="Sartist">
                    {{currentItem.artist}}
                  </p>
                </div>
              </div>
            </div>
          </div>

          <div class="player_controler_wrap">
            <div class="player_controler">
              <div class="play_pause" @click="PLAY_PAUSE" :class="paused?'play':'pause'">
              </div>
              <div class="player_controler_pro_wrap">
                <div class="player_controler_pro">
                  <label for="Progress" >-{{leftTime}}</label>
                  <Progress :value="currentPercentAbsoulte" @changeProgress = 'changeProgress'></Progress>
                  <label for="Progress" >{{durationTime}}</label>
                </div>
              </div>
              <div class="left_right_wrap">
                <div class="player_left"></div>
                <div class="player_right"></div>
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>
<script>
import './Player.scss'
import Progress from './../Progress/Progress.jsx'
import {mapGetters, mapState, mapMutations} from 'vuex'
export default {
  components: {
    Progress
  },
  computed: {
    ...mapGetters('list', ['currentItem']),
    ...mapState('player', ['paused']),
    ...mapGetters('player', ['leftTime', 'currentPercentAbsoulte', 'durationTime'])
  },
  methods: {
    ...mapMutations('player', ['PLAY_PAUSE', 'CHANGE_PROGRESS']),
    changeProgress (progress) {
      this.CHANGE_PROGRESS(progress)
    }
  }
}
</script>



